<template><section><h2>ActionSheet 动作面板</h2>
<template>
  <z-button :text="'打开动作面板'" :type="'success'" @click.native="isShowActionSheet = true">
  </z-button>
<p><z-actionsheet :show="isShowActionSheet" :title="'动作面板展示'" :lis="lis" :cancel-button="'确定或者取消或者其他'" @click998="test" @after-close="isShowActionSheet = false">
</z-actionsheet>
</p></template><p></p>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>
<span class="hljs-attr">:text</span>=<span class="hljs-string">"'打开动作面板'"</span>
<span class="hljs-attr">:type</span>=<span class="hljs-string">"'success'"</span>
<span class="hljs-attr">@click.native</span>=<span class="hljs-string">"isShowActionSheet = true"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">z-actionsheet</span>
<span class="hljs-attr">:show</span>=<span class="hljs-string">"isShowActionSheet"</span>
<span class="hljs-attr">:title</span>=<span class="hljs-string">"'动作面板展示'"</span>
<span class="hljs-attr">:lis</span>=<span class="hljs-string">"lis"</span>
<span class="hljs-attr">:cancel-button</span>=<span class="hljs-string">"'确定或者取消或者其他'"</span>
<span class="hljs-attr">@click998</span>=<span class="hljs-string">"test"</span>
<span class="hljs-attr">@after-close</span>=<span class="hljs-string">"isShowActionSheet = false"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-actionsheet</span>&gt;</span>
</code></pre>
<pre><code class="language-js">data() {
  <span class="hljs-keyword">return</span> {
    <span class="hljs-attr">isShowActionSheet</span>: <span class="hljs-literal">false</span>,
    <span class="hljs-attr">lis</span>: [
      {
        <span class="hljs-attr">id</span>: <span class="hljs-number">998</span>,
        <span class="hljs-attr">text</span>: <span class="hljs-string">'私聊'</span>
      },
      {
        <span class="hljs-attr">text</span>: <span class="hljs-string">'关注'</span>,
        <span class="hljs-attr">href</span>: <span class="hljs-string">'http://blog.zhaoyuxiang.cn'</span>
      },
      {
        <span class="hljs-attr">text</span>: <span class="hljs-string">'拉入黑名单'</span>
      }
    ]
  };
},

<span class="hljs-attr">methods</span>: {
  test() {
    alert(<span class="hljs-string">'点击了标识为998的item'</span>)
  }
}
</code></pre>
<h3>API</h3>
<template>
  <z-table :title="'主要参数'" :ths="['参数','类型','必填','默认值','说明']" :trs="[
    ['show','Boolean','是','-','是否显示动作面板'],
    ['lis','Array','是','-','动作面板中的各项条目'],
    ['title','String','否','-','动作面板的标题'],
    ['cancel-button','String','否','取消','动作面板关闭按钮的文字']
  ]">
  </z-table>
<p><z-table :title="'lis中的参数'" :ths="['参数','类型','必填','默认值','说明']" :trs="[
    ['text','String','是','-','条目显示的文字'],
    ['id','Number','否','-','条目的标识'],
    ['href','String','否','-','条目要跳转到的路径']
  ]">
</z-table>
</p></template><p></p>
<h4>关闭动作面板的方法</h4>
<p>当关闭面板的按钮被点击时，组件会emit一个&quot;after-close&quot;，在父组件接收即可。</p>
<h4>给条目追加点击事件的方法</h4>
<p>如果想对某个条目绑定click方法，只需要给该条目一个标识(id),当该条目被点击时，组件会emit一个&quot;'click' + id&quot;,在父组件接收即可。</p>
<p>比如例子中我们给第一个条目标识为998，那么只需要在父组件<code>@click998</code>即可。</p>
</section></template>

<script>
export default {
  data() {
    return {
      isShowActionSheet: false,
      lis: [
        {
          id: 998,
          text: '私聊'
        },
        {
          text: '关注',
          href: 'http://blog.zhaoyuxiang.cn'
        },
        {
          text: '拉入黑名单'
        }
      ]
    };
  },

  methods: {
    test() {
      alert('点击了标识为998的item')
    }
  }
};
</script>


